var React = require("react");
var Router = require("react-router");
var ItemsFilters = require("../consts/itemListFilters");
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;
var RouteHandler = Router.RouteHandler;
var Link = Router.Link;

var filtersArray = ItemsFilters.getFilters();

var styles = {
    activeNavItemLink: {
        textDecoration: "none",
        cursor: "default"
    },
    navItemLi: {
        display: "inline-block",
        marginRight: "10px"
    }
};

var itemListFilters = React.createClass({
    displayName: "ItemsNav",

    propTypes: {
        disabled: React.PropTypes.bool.isRequired
    },

    mixins: [PureRenderMixin],

    render: function () {
        var self = this;
        console.log("nav - render()");
        var navItems = filtersArray.map(function (filter) {
            if (self.props.disabled) {
                return (
                    <li key={filter.name} style={styles.navItemLi}>{filter.title}</li>
                );
            }

            return (
                <li key={filter.name} style={styles.navItemLi}>
                    <Link
                        to="items"
                        activeStyle={styles.activeNavItemLink}
                        params={{filter: filter.name}}>{filter.title}</Link>
                </li>
            );
        });

        return (
            <ul>{navItems}</ul>
        );
    }
});

module.exports = itemListFilters;



