var React = require("react");
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;
var ItemListItem = require("./itemListItem");
var masonry = require("../../common/utils/masonry");

var style = {
    display: "inline-block",
    verticalAlign: "top",
    width: "240px",
    padding: 0,
    margin: 0
};

var ItemsList = React.createClass({
    displayName: "ItemsList",

    propTypes: {
        amtColumns: React.PropTypes.number.isRequired,
        items: React.PropTypes.array.isRequired
    },

    mixins: [PureRenderMixin],

    render: function () {
        console.log("ItemsList render()", this.props.amtColumns, "columns");

        var columns = masonry(this.props.items, this.props.amtColumns, "height")
            .map(function (column, idx) {
                var columnKey = "column" + idx;
                var columnItems = column.map(function (item) {
                    return (
                        <ItemListItem
                            key={item.id}
                            caption={item.caption}
                            imageSrc={item.medium_url}
                            width={item.width}
                            height={item.height}
                            />
                    );
                });
                return (<div style={style} key={columnKey}>{columnItems}</div>)
            });
        return (<div>{columns}</div>);
    }
});

module.exports = ItemsList;



