var React = require("react");
var Router = require("react-router");
var Items = require("./items/app");

var Route = Router.Route;
var DefaultRoute = Router.DefaultRoute;
var NotFoundRoute = Router.NotFoundRoute;

var defaultItemsRoute = React.createClass({
    statics: {
        willTransitionTo(transition) {
            transition.redirect("items", {filter: "popular"});
        }
    },
    render() { return null; }
});

var routes = (
    <Route handler={Items}>
        <Route name="items" path="/items/:filter" handler={Items} />
        <DefaultRoute handler={defaultItemsRoute}/>
    </Route>
);

module.exports = {
    run: function (el) {
        Router.run(routes, function (Handler, state) {
            React.render(<Handler params={state.params} />, el);
        });
    }
};



