var React = require("react");
var Router = require("react-router");
var DocumentTitle = require("react-document-title");
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;
var RouteHandler = Router.RouteHandler;
var Nav = require("./list/itemListFilters");
var ItemsList = require("./list/itemsList");
var NoItemsFound = require("./list/noItemsFound");
var Processing = require("./list/processing");
var ItemsStore = require("./stores/store");
var Actions = require("./actions/actions");
var ItemsFilters = require("./consts/itemListFilters");
var MidierCallStatuses = require("../common/consts/midtierCallStatuses");
var windowResizeMixin = require("../common/utils/windowResizeMixin");

var itemColumnWidth = 245;

var Items = React.createClass({
    displayName: "Items",
    propTypes: {},
    contextTypes: {
        router: React.PropTypes.func
    },
    mixins: [ItemsStore.mixin, PureRenderMixin, windowResizeMixin],

    storeDidChange: function () {
        this.setState(this.getState());
    },

    getInitialState: function () {
        return this.getState();
    },

    getState: function () {
        var existingAmtColumns = (this.state && this.state.amtColumns) || 0;
        return {
            items: ItemsStore.items,
            itemsFetchStatus: ItemsStore.itemsFetchStatus,
            amtColumns: existingAmtColumns
        };
    },

    componentDidMount: function () {
        this.changeFilterFromProps();
    },

    componentWillReceiveProps: function () {
        this.changeFilterFromProps();
    },

    getFilter: function () {
        return this.context.router.getCurrentParams().filter;
    },

    onComponentResize: function(dimensions) {
        var amtColumns = Math.floor(dimensions.width / itemColumnWidth);
        var state = this.getState();

        if(amtColumns == state.amtColumns) return;

        state.amtColumns = amtColumns;
        this.setState(state);
        console.log("onResize() amtColumns", this.state.amtColumns);
    },

    changeFilterFromProps: function () {
        var filter = this.getFilter();
        if (!filter) return;
        console.log("changeFilterFromProps() ", filter);
        Actions.getItems(filter);
    },

    renderItems: function () {
        var processing = this.state.itemsFetchStatus === MidierCallStatuses.processing;
        if (processing)
            return (<Processing />);

        if (this.state.items.size === 0)
            return (<NoItemsFound />);

        if (!this.state.amtColumns)
            return (<div></div>);

        return (
            <ItemsList
                amtColumns={this.state.amtColumns}
                items={this.state.items.toArray()}
                />
        );
    },

    render: function () {
        var processing = this.state.itemsFetchStatus === MidierCallStatuses.processing;
        console.log("Items render() processing:", processing);

        var items = this.renderItems();

        var title = "Items";
        var filter = this.getFilter();
        if (filter)
            title += ": " + ItemsFilters.getFilterByName(filter);

        return (
            <div>
                <DocumentTitle title={title}/>
                <Nav disabled={processing}/>
                {items}
            </div>
        );
    }
});

module.exports = Items;



