var React = require("react");
var Router = require("react-router");
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;

var style = {
    color: "purple"
};

var noItemsFound = React.createClass({
    displayName: "NoItemsFound",

    mixins: [PureRenderMixin],

    render: function () {
        return (
            <div style={style}>Sorry, no items found. We should probably hide this link if you don't have any items.</div>
        );
    }
});

module.exports = noItemsFound;



